<template>
    <div id="streams">
        <div class="contenedor">
            <div class="concierto" v-for="concierto of conciertos" :key="concierto.id" @mouseover="playVideo('video' + concierto.id)" @mouseleave="stopVideo('video' + concierto.id)">
                <div class="imagen">
                    <img :src="concierto.location" alt="">  
                    <h3 class="titulo">{{concierto.title}}</h3>
                    <video v-bind:id="('video' + concierto.id)" muted>
                        <source :src="concierto.video" type="video/mp4">
                    </video>
                </div>
                <div class="container-descrip">
                    <p class="descripcion">{{concierto.description}}</p>
                    <div>
                        <p class="precio">{{concierto.precio}}</p>
                        <button class="btn-comprar">Comprar</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'tarjetas',
    data() {
        return {
            conciertos: [
                {
                    id: '1',
                    title: 'Beatles',
                    description: 'Una descripcion para el show que va a venir, mas larga que las demas para testear.',
                    precio: '2500$',
                    location: require("@/assets/conciertos/beatles.jpg"),
                    video: require("@/assets/conciertos/queen.mp4")
                },
                {
                    id: '2',
                    title: 'Red hot chilli peppers',
                    description: 'Una descripcion para el show que va a venir.',
                    precio: '5000$',
                    location: require("@/assets/conciertos/rhcp.jpg"),
                    video: require("@/assets/conciertos/queen.mp4")
                },
                {
                    id: '3',
                    title: 'Queen',
                    description: 'Una descripcion para el show que va a venir.',
                    precio: '4500$',
                    location: require("@/assets/conciertos/queen.jpg"),
                    video: require("@/assets/conciertos/queen.mp4")
                }
                ,
                {
                    id: '4',
                    title: 'Queen',
                    description: 'Una descripcion para el show que va a venir.',
                    precio: '4500$',
                    location: require("@/assets/conciertos/queen.jpg"),
                    video: require("@/assets/conciertos/queen.mp4")
                }
            ]
        }
    },
    methods: {
        playVideo: function (videoid) {
            var vid = document.getElementById(videoid);
            vid.play();
        },
        stopVideo: function(videoid) {
            var vid = document.getElementById(videoid);
            vid.pause();
        }
    }
}
</script>

<style lang="scss">
    #streams{
        margin-top: 40px;
        .contenedor {
            max-width: 1100px;
            margin: auto;
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            .concierto {
                cursor:pointer;
                background: white;
                box-shadow: 1px 1px 5px #ddd;
                max-width: 250px;
                text-align: center;
                border-radius: 5px;
                overflow: hidden;
                margin-bottom: 40px;
                transition: all 0.5s;
                .imagen {
                    position: relative;
                    img {
                        max-height: 180px;
                        max-width: 250px;
                        width: 100%;
                        transition: all 0.5s;
                        opacity: 1;
                        visibility: visible;
                    }
                    .titulo {
                        position: absolute;
                        bottom: 10px;
                        left: 20px;
                        background: #6f6f6e;
                        color: white;
                        padding: 0 4px;
                        max-width: 70%;
                        font-family: Source-Code-Variable;
                        font-size: 22px;
                        transition: all 0.5s;
                        opacity: 1;
                        visibility: visible;
                    }
                    video {
                        position: absolute;
                        width: 300px;
                        height: auto;
                        top: 0px;
                        left: 0;
                        opacity: 0;
                        transition: all 0.5s;
                        visibility: hidden;
                        transition-delay: 0s;
                        border-radius: 5px 5px 0 0;
                    }
                }
                .container-descrip {
                    display: flex;
                    height: calc(100% - 200px);
                    justify-content: space-between;
                    align-items: center;
                    flex-direction: column;
                    transition: all 0.5s;
                    .descripcion{
                        color: #666;
                        padding: 15px;
                        margin: 0;
                        font-size: 14px;
                    }
                    .precio {
                        padding: 0 10px;
                        font-size: 30px;
                        line-height: 30px;
                    }
                    .btn-comprar {
                        margin-bottom: 20px;
                        border:none;
                        background-color: #55b35b;
                        color: white;
                        padding: 8px 15px;
                        border-radius: 2px;
                    }
                    .btn-comprar:focus {
                        outline: none;
                    }
                }
            }
            .concierto:hover {
                max-width: 300px;
                .imagen {
                    img {
                        max-height: 225px;
                        max-width: 300px;
                        // opacity: 0;
                        // visibility: hidden;
                    }
                    .titulo {
                        opacity: 0;
                        visibility: hidden;
                    }
                    video {
                        opacity: 1;
                        visibility: visible;
                        transition-delay: 0.3s;
                        
                    }
                }
                .container-descrip {
                    height: calc(100% - 250px); 
                }
            }
        }
    }
</style>